<template>
  <div class="searchRoot">
    <div class="searchTop">
      <svg class="icon" aria-hidden="true" @click="$router.push('/')">
        <use xlink:href="#icon-fanhui"></use>
      </svg>
      <div class="searchBox">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-sousuo-copy"></use>
        </svg>
        <input
          type="text"
          @keydown.enter="searchKeyWord"
          v-model="KeyWord"
          placeholder="杨和苏"
          data-auto-clear-button="true"
        />
      </div>
      <span @click="searchKeyWord">搜索</span>
    </div>
    <div class="foundMusic">
      <span v-for="(item,i) in searchList" :key="i" @click="updateIndex(item,i)">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-sousuo-copy"></use>
        </svg>
        <p>{{item.artists[0].name}}</p>
        <p style="color: #fff;">{{ item.name }}</p>
      </span>
    </div>
    <div class="musicPart">
      <span>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-geshou2"></use>
        </svg>
        <p>歌手</p>
      </span>
      <span>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-a-24gf-musicAlbum"></use>
        </svg>
        <p>曲风</p>
      </span>
      <span>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-yinfu"></use>
        </svg>
        <p>专区</p>
      </span>
      <span style="border: none">
        <svg class="icon" aria-hidden="true" style="padding: 0">
          <use xlink:href="#icon-tinggeshiqu-copy"></use>
        </svg>
        <p>识曲</p>
      </span>
    </div>
    <div class="searchHistory">
      <div class="historyTop">
        <span>搜索历史</span>
        <svg class="icon" aria-hidden="true" @click="delHistory">
          <use xlink:href="#icon-shanchu-copy"></use>
        </svg>
      </div>
      <div class="historyContent">
        <span
          v-for="item in keyWordList"
          :key="item"
          @click="searchHistory(item)"
        >
          {{ item }}
        </span>
      </div>
    </div>
    <div class="HotMusic">
      <div class="HotTop">
        <p style="padding-right: 20px">热搜榜</p>
        <span>
          <svg class="icon" aria-hidden="true" @click="$router.push('/')">
            <use xlink:href="#icon-bofang1"></use>
          </svg>
          <p>播放</p>
        </span>
      </div>
      <hr
        style="
          background-color: rgba(255, 255, 255, 0.195);
          height: 1px;
          border: none;
          margin: 5px 10px;
        "
      />
      <div class="HotList">
        <span v-for="(item1, i) in HotList" :key="i">
          <p>{{ i + 1 }}</p>
          {{ item1.searchWord }}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import { getSearchMusic } from "@/axios/api/home.js";
import { getSearchHot } from "@/axios/api/home";
export default {
  data() {
    return {
      keyWordList: [],
      KeyWord: "",
      searchList: [],
      HotList: [],
    };
  },
  mounted() {
    this.keyWordList = JSON.parse(localStorage.getItem("keyWordList") || "[]");
    this.searchHot();
  },
  methods: {
    //搜索功能实现
    searchKeyWord: async function () {
      if (this.KeyWord != "") {
        this.keyWordList.unshift(this.KeyWord);
        //去重
        this.keyWordList = [...new Set(this.keyWordList)];
        //固定长度
        if (this.keyWordList.length > 10) {
          this.keyWordList.splice(this.keyWordList.length - 1, 1);
        }
        localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList));
        let res = await getSearchMusic(this.KeyWord);
        console.log(res);
        this.searchList = res.data.result.songs;
        this.KeyWord = "";
      }
    },
    //搜索功能实现（点击span也能进行搜索）
    searchHistory: async function (item) {
      let res = await getSearchMusic(item);
      this.searchList = res.data.result.songs;
      console.log(res);
      this.KeyWord = "";
    },
    searchHot: async function () {
      let res = await getSearchHot();
      this.HotList = res.data.data;
      // console.log(this.HotList);
    },
    //点击搜索出来的歌曲进行播放
    updateIndex(item){
      item.al = item.album
      item.al.picUrl = item.album.artist.img1vUrl
      this.$store.commit("pushPlayList",item)
      this.$store.commit("updatePlayListIndex",this.$store.state.playList.length-1)
    },
    //删除记录
    delHistory() {
      localStorage.removeItem("keyWordList");
      this.keyWordList = [];
    },
  },
};
</script>

<style lang="less" scoped>
.searchRoot {
  width: 100%;
  background-color: rgb(35, 28, 28);
  .searchTop {
    color: #fff;
    display: flex;
    justify-content: space-between;
    padding: 15px;
    align-items: center;
    .icon {
      width: 0.4rem;
      height: 0.4rem;
    }
    .searchBox {
      border-radius: 20px;
      background-color: #6b6b6b91;
      height: 100%;
      width: 75%;
      padding: 7px 10px 7px 10px;
      position: relative;
      .icon {
        align-items: center;
        width: 0.27rem;
        height: 0.27rem;
        line-height: 10px;
        top: 9px;
        position: absolute;
      }
      input {
        margin-left: 20px;
        border: 0;
        outline: none;
        font-size: 14px;
        width: 87%;
        color: #d8d8d8da;
        background-color: #ffffff00;
      }
    }
    span {
      font-size: 15px;
    }
  }
  .foundMusic{
    padding: 10px;
    span{
      color: #ccccccca;
      display: flex;
      justify-content: left;
      padding: 15px;
      border-bottom: 1px solid rgba(204, 204, 204, 0.041);
      .icon{
        padding: 0 3px;
        margin: 0 3px;
      }
      p{

      }
    }
  }
  .icon-sousuo-copy{}
  .musicPart {
    display: flex;
    justify-content: space-around;
    span {
      display: flex;
      padding: 0px 15px;
      width: 25%;
      border-right: 1px solid #ffffff3d;
      justify-content: center;
      .icon {
        padding: 3px;
      }
      p {
        color: #f3f3f3;
        font-size: 13px;
        font-weight: 700;
      }
    }
  }
  .searchHistory {
    padding: 20px;
    .historyTop {
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        color: #f3f3f3;
        font-size: 13px;
        font-weight: 700;
        padding: 0px;
      }
      .icon {
        padding: 3px;
      }
    }
    .historyContent {
      margin-top: 10px;
      span {
        color: #fff;
        padding: 5px 10px;
        margin: 5px 5px;
        background-color: #ffffff2b;
        border-radius: 10px;
        display: inline-block;
        font-size: 12px;
      }
    }
  }
  .HotMusic {
    padding: 10px;
    width: 90%;
    margin: 0 auto;
    background-color: #84848425;
    border-radius: 10px;
    margin-bottom: 1.2rem;
    .HotTop {
      display: flex;
      justify-content: left;
      padding: 10px;
      p {
        color: #f3f3f3;
        font-size: 17px;
        font-weight: 700;
      }
      span {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1px 5px;
        background-color: #ffffff15;
        border-radius: 10px;
        .icon {
          width: 0.3rem;
          height: 0.3rem;
          padding: 3.5px;
        }
        p {
          color: #f3f3f3;
          font-size: 12px;
        }
      }
    }
    .HotList {
      color: #cccccc;
      span:nth-child(-n + 3) {
        font-weight: 700;
        p {
          color: #ea3030;
          font-weight: 700;
          font-size: 20px;
        }
      }
      p {
        display: inline-block;
        padding-right: 10px;
        font-size: 17px;
      }
      span {
        display: block;
        padding: 10px;
        color: rgba(255, 255, 255, 0.805);
      }
    }
  }
}
</style>